.table {
	font-size   : 14px;
	font-weight : 600;
}

thead th {
	text-transform : uppercase;
}

th, td {
	vertical-align : middle;
}


.table-col {
	display        : flex;
	flex-direction : column;
}

.table-col > span:nth-of-type(1) {
	font-size : 14px;

}

.table-col > span:nth-of-type(2), .table-col > span:nth-of-type(3) {
	font-size : 12px;
	color     : #757575
}

thead .table-col {
	align-items : flex-start;
}

thead .table-col > span:nth-of-type(2) {
	font-size : 10px;
}

.form-label {
	font-size : 12px;
	color     : #757575;
}

.form-label em {
	color : red;
}

@media screen and (max-width : 991px) {
	table thead {
		border   : none;
		clip     : rect(0 0 0 0);
		height   : 1px;
		margin   : -1px;
		overflow : hidden;
		padding  : 0;
		position : absolute;
		width    : 1px;
	}

	table td {
		border-bottom : 1px solid #DDDDDD;
		display       : block;
	}

	table td::before {
		content        : attr(data-label);
		float          : left;
		font-weight    : bold;
		text-transform : uppercase;
		width          : 120px;
	}
}
